.timer {
  display: flex;
  justify-content: center;

  padding: 70px 0 0 0;
}

.field:not(:last-child) {
  margin-right: 30px;
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 150px;

  background-color: darkblue;
  border-radius: 25px;

  color: white;
  font-family: 'Architects Daughter', cursive;
  line-height: 1.3;
}

.value {
  font-size: 48px;
}

.label {
  font-size: 24px;
}

.timer-date {
  display: flex;
  align-items: center;
  flex-direction: column;

  padding: 75px 0 0 0;
}

.timer-text {
  font-family: 'Architects Daughter', cursive;
  color: black;
  font-size: 20px;
}

.timer-text-desc {
  font-family: 'Architects Daughter', cursive;
  color: black;
}

.timer-btn {
  font-family: 'Architects Daughter', cursive;
  color: white;
  background-color: darkblue;

  border: 1px solid transparent;
  border-radius: 5px;
}
